<script>
  import { FirebaseApp, User, Doc, Collection } from "sveltefire";
  import firebase from "firebase/app";
  import "firebase/firestore";
  import "firebase/auth";
  import "firebase/performance";
  import "firebase/analytics";
  const db = firebase.firestore();


export let alumno;
export let materia;
/* Enviar ciclo lectivo actual a la tabla notas */
var today = new Date();
var year = today.getFullYear();

</script>


  


  <Doc path={`alumnos/${alumno}`} let:data={alumnos} let:ref={alumnosRef}>
    <div slot="loading">
      <div uk-spinner />
    </div>

<!-- Filtra la tabla notas por materia y ciclo lectivo actual -->
    <Collection
      path={alumnosRef.collection('notas')}
      query={ref => ref.where('materia', '==', `${materia}`).where('cicloLectivo','==', year).orderBy('trimestre','asc')}
      let:data
      let:ref
      on:data
      on:ref
      log>

      <div slot="loading">
        <div uk-spinner />
      </div>

      <div class="uk-overflow-auto">
        <table
          class="uk-table uk-table-striped uk-table-condensed uk-text-nowrap">
          <caption />
          <thead>
            <tr>
              <th>Fecha</th>
              <th>Trimestre</th>
              <th>Nota</th>
              <th>Op</th>
            </tr>
          </thead>
          <tbody>
            {#each data as nota}
              <tr>
                <td>
                  {new Date(nota.created).toLocaleString('es-ar', {
                    year: 'numeric',
                    month: '2-digit',
                    day: '2-digit'
                  })}
                </td>
                <td>{nota.trimestre}° Trimestre</td>
                <td>{nota.nota}</td>
                <td>
                  <button
                    uk-icon="icon: close"
                    uk-tooltip="Eliminar.."
                    on:click={() => nota.ref.delete()
                    .then(resp=>{
                          UIkit.notification({
                            message:
                                "<span uk-icon='icon: check'></span> Nota eliminada con éxito.",
                            pos: "top-center",
                            status: "danger"
                            });
                        })
                    } />
                </td>
              </tr>
            {/each}
          </tbody>
        </table>
      </div>
    </Collection>
  </Doc>

